import React, { FC, useContext } from 'react';
import { useTranslation } from 'react-i18next';

import { PriceWrapper, PriceTextWrapper, PriceDivider, PriceText } from './styles';
import { IPlan } from '../../../../../interfaces/plan';
import { userContext } from '../../../../../state';
import { IPlanPriceParams } from '../../../../pricing/components/payment-modal';

interface IPriceList {
  getPlanPeriodPrice: (params: IPlanPriceParams) => string|number;
  pricingPlan: IPlan;
  isAnnual: boolean;
  isStripePayment?: boolean;
}

const PriceList: FC<IPriceList> = (props) => {
  const {
    getPlanPeriodPrice,
    pricingPlan: { name },
    isAnnual,
    isStripePayment,
  } = props;

  const { email = '' } = useContext(userContext);
  const { t: translation } = useTranslation();

  return (
    <PriceWrapper isStripePayment={!!isStripePayment}>
      <PriceTextWrapper>
        <PriceText>
          {translation('pricing.checkoutModal.accountName')}
        </PriceText>
        <PriceDivider />
        <PriceText>
          {email}
        </PriceText>
      </PriceTextWrapper>
      <PriceTextWrapper>
        <PriceText>
          {isAnnual ?
            `${name} ${translation('pricing.checkoutModal.billedAnnually')}` :
            `GoLogin ${name}`}
        </PriceText>
        <PriceDivider />
        <PriceText>
          {getPlanPeriodPrice({ isAnnual, isDiscount: false })}
        </PriceText>
      </PriceTextWrapper>
      <PriceTextWrapper>
        <PriceText>
          {translation('pricing.checkoutModal.discountTitle')}
        </PriceText>
        <PriceDivider />
        <PriceText>
          {isAnnual ?
            `-${getPlanPeriodPrice({ isAnnual, isDiscount: true })}` :
            ''}
        </PriceText>
      </PriceTextWrapper>
      <PriceTextWrapper>
        <PriceText>
          {translation('pricing.checkoutModal.chargedTitle')}
        </PriceText>
        <PriceDivider />
        <PriceText>
          {getPlanPeriodPrice({ isAnnual, isDiscount: true })}
        </PriceText>
      </PriceTextWrapper>
    </PriceWrapper>
  );
};

export default PriceList;
