/* eslint-disable max-len */
import * as React from 'react';

import { IIconWrapper, IconWrapper } from './wrapper';

const IconWhatsApp = (props: IIconWrapper): JSX.Element => (
  <IconWrapper {...props}>
    <svg
      width={48}
      height={48}
      viewBox="0 0 48 48"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      <rect width={48} height={48} rx={24} fill="url(#wpa)" />
      <defs>
        <pattern
          id="wpa"
          patternContentUnits="objectBoundingBox"
          width={1}
          height={1}
        >
          <use xlinkHref="#wpb" transform="scale(.00444)" />
        </pattern>
        <image
          id="wpb"
          width={225}
          height={225}
          xlinkHref="data:image/png;base64,"
        />
      </defs>
    </svg>
  </IconWrapper>
);

export default IconWhatsApp;
