export interface IPaymentBaseClass {
  plan: string;
  needChangePlan: boolean;
  setModalUrl: (url: string) => void;
  toggleModal: (status: boolean) => void;
  toggleModalWindow: (status: boolean, url: string) => void;
  redirectTo: () => void;
  makeRedirect: () => void;
}

export class PaymentBaseClass implements IPaymentBaseClass {
  constructor(
    private plan: string,
    private discount: string,
    private needChangePlan: boolean,
    private setModalUrl: (url: string) => void,
    private toggleModal: (status: boolean) => void,
    private redirectTo: () => void,
  ) {}

  public toggleModalWindow(status: boolean, url: string) {
    this.setModalUrl(url);
    this.toggleModal(status);
  }

  public makeRedirect() {
    if (this.redirectTo) {
      this.redirectTo();
    }
  }
}
