import { useTranslate } from '@tolgee/react';
import { message } from 'antd';
import { MessageType } from 'antd/es/message';
import React, { Dispatch, FC, SetStateAction, useContext, useState } from 'react';

import { IPlanPriceParams } from './payment-modal';
import { PaypalContainerOld, PaypalContainer, ButtonPayPaypal, TextPoweredByPaypal, ContainerTextPoweredByPaypal } from './styles';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { sendGA4PurchaseEvent } from '../../../GA4';
import { IPlan } from '../../../interfaces/plan';
import { userContext, workspaceContext } from '../../../state';
import { IconPaypalLogo } from '../../../ui/gologin-header/icons/icon-paypal-logo';
import { IconPaypalLogoSmall } from '../../../ui/gologin-header/icons/icon-paypal-logo-small';
import { PAYPAL_SUBSCRIPTIONS_IDS } from '../../../utils/paypal-subscription-ids';
import { sendActionAnalytics, sendErrorPaymentAnalytics } from '../../common/api';
import PriceList from '../../modalsComponents/components/checkout-modal/price-list';
import { Spinner } from '../../modalsComponents/components/checkout-modal/styles';
import {
  checkPlanMaxProfiles,
  isDiscountAnnual,
  getSelectedPlan,
} from '../../modalsComponents/components/checkout-modal/utils';
import { changePlan, checkPaypalOrder, createPaypalSubIntent } from '../api';

const isElectron = !!window.require;

interface IPaypalForm {
  selectedPlan: string;
  selectedDiscount: string;
  plans: IPlan[];
  getCheckmarkLine?: () => JSX.Element;
  getPlanPeriodPrice?: (params: IPlanPriceParams) => string|number;
  pricingPlan?: IPlan;
  workspaceId?: string;
  setIsLoadingModalShown: Dispatch<SetStateAction<boolean>>;
  setShowSuccessModal: Dispatch<SetStateAction<boolean>>;
}

const PaypalForm: FC<IPaypalForm> = (props) => {
  const [isBtnLoading, setIsBtnLoading] = useState<boolean>(false);

  const {
    selectedPlan,
    selectedDiscount,
    plans,
    getCheckmarkLine,
    getPlanPeriodPrice,
    pricingPlan,
    workspaceId = '',
    setIsLoadingModalShown,
    setShowSuccessModal,
  } = props;

  const {
    profiles: userProfilesCount = 100,
    _id = '',
    updateUser,
  } = useContext(userContext);

  const { profilesCount: workspaceProfilesCount, updateWorkspace, updateAvailableWorkspace } = useContext(workspaceContext);
  const { t: translation } = useTranslate();

  const isAnnual = isDiscountAnnual(selectedDiscount);
  const planPeriod = isAnnual ? 'annual' : 'monthly';

  const checkPayment = (data: any): void => {
    setIsLoadingModalShown(true);
    const orderId = data.orderID;
    const subscriptionId = data.subscriptionID || '';
    changePlan(selectedPlan, workspaceId)
      .finally(() =>
        checkPaypalOrder({
          planId: selectedPlan,
          discountId: selectedDiscount,
          orderId,
          subscriptionId,
          workspaceId,
        })
          .then((checkPaypalData) => {
            updateUser({
              plan: getSelectedPlan(plans, selectedPlan),
              hasTrial: false,
              trialDays: 0,
              planExpireDate: checkPaypalData.body.visibleExpiresDate,
              paypalSubscriptionId: subscriptionId,
            });

            const { id, name, maxProfiles = 0 } = getSelectedPlan(plans, selectedPlan);
            if (workspaceId) {
              updateWorkspace({
                isUnpaid: false,
                paymentIsTrial: false,
                planExpiresAt: checkPaypalData.body.visibleExpiresDate,
              });

              updateAvailableWorkspace(workspaceId, {
                planId: id,
                visibleExpiresDate: checkPaypalData.body.visibleExpiresDate,
                planName: name,
                planProfilesMax: maxProfiles,
              });
            }

            setShowSuccessModal(true);

            const { paymentId = '', visibleAmount = 0.00 } = checkPaypalData.body || {};
            sendGA4PurchaseEvent({
              userId: _id?.toString(),
              amount: visibleAmount,
              planId: selectedPlan,
              planName: name,
              paymentId,
            });
          })
          .catch((error) => {
            setIsLoadingModalShown(false);
            message.error(error.message);
            sendErrorPaymentAnalytics(
              error.message,
              {
                plan: selectedPlan,
                planPeriod,
                scenario: 'checkPaypalOrder',
              },
              'paypal',
            );
          }),
      );
  };

  const getPaypalSubscriptionId = (): string|undefined => {
    const paypalSubscriptionObj = PAYPAL_SUBSCRIPTIONS_IDS.find(({ planId }) => planId === selectedPlan);
    if (!paypalSubscriptionObj) {
      return;
    }

    const { links } = paypalSubscriptionObj;
    const discountLinkObj = links.find(({ discountId }) => discountId === selectedDiscount);
    if (!discountLinkObj) {
      return;
    }

    return discountLinkObj.subscriptionId;
  };

  const paypalCustomData = [_id, selectedPlan, selectedDiscount, workspaceId];

  const getPaypalPaymentLink = async (): Promise<void> => {
    sendActionAnalytics('clicked pay now', { actionInfo: 'paypal', paymentMethod: 'paypal' }).catch(() => null);
    setIsBtnLoading(true);
    setTimeout(() => {
      setIsBtnLoading(false);
    }, 3000);

    const paypalSubId = getPaypalSubscriptionId() || '';
    if (!paypalSubId) {
      message.error(translation('notifications.error.paypalSubscriptionNotReady'));
    }

    const { paymentLink = '' } = await createPaypalSubIntent(paypalSubId, paypalCustomData);
    if (!paymentLink || paymentLink.includes('error')) {
      message.error(translation('notifications.error.paypalSubscriptionNotReady'));

      return;
    }

    if (isElectron) {
      window.require('electron').shell.openExternal(paymentLink);

      return;
    }

    window.open(paymentLink);
  };

  const getPaypalButton = (): JSX.Element => (
    <>
      <ButtonPayPaypal
        onClick={getPaypalPaymentLink}>
        {isBtnLoading ?
          <Spinner size={19} thickness={2} /> :
          <IconPaypalLogo />}
      </ButtonPayPaypal>
      <ContainerTextPoweredByPaypal>
        <TextPoweredByPaypal>
          Developed by
        </TextPoweredByPaypal>
        <IconPaypalLogoSmall />
      </ContainerTextPoweredByPaypal>
    </>
  );

  const getContent = (): JSX.Element|null => {
    if (NEW_FEATURES.pricing) {
      if (!(getPlanPeriodPrice && pricingPlan)) {
        return null;
      }

      return (
        <>
          <PriceList
            getPlanPeriodPrice={getPlanPeriodPrice}
            pricingPlan={pricingPlan}
            isAnnual={isAnnual}
          />
          <PaypalContainer>
            {getPaypalButton()}
          </PaypalContainer>
        </>
      );
    }

    return (
      <>
        {getCheckmarkLine ? getCheckmarkLine() : null}
        <PaypalContainerOld paddingBottom={isAnnual}>
          {getPaypalButton()}
        </PaypalContainerOld>
      </>
    );
  };

  return getContent();
};

export default PaypalForm;
