import { useTranslate } from '@tolgee/react';
import { message } from 'antd';
import React, { Dispatch, FC, MouseEvent, SetStateAction, useContext, useEffect, useState } from 'react';
import { Elements, injectStripe, StripeProvider } from 'react-stripe-elements';

import {
  ModalDivider,
  ModalUpgradeButtonContainer,
  ModalUpgradeCardPaymentButton,
} from './domElements';
import {
  Col,
  ParamsCardLineContainer,
  PaymentDetailsContainer,
  PaymentDetailsTxt,
  Row,
  StyledCardElement,
} from './styles';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { sendGA4PurchaseEvent } from '../../../GA4';
import { useQuery } from '../../../hooks';
import { IPlan } from '../../../interfaces/plan';
import { history } from '../../../services';
import { useCurrentTabShownCurrency, userContext, workspaceContext } from '../../../state';
import IconStripe from '../../../ui/icons/IconStripe';
import { getGoogleAnalyticsSessionId, sendActionAnalytics, sendErrorPaymentAnalytics } from '../../common/api';
import { StripeManager } from '../../common/stripe-manager';
import { Spinner } from '../../modalsComponents/components/checkout-modal/styles';
import {
  checkPlanMaxProfiles,
  isDiscountAnnual,
  getSelectedPlan,
  updateSelectedPlan,
} from '../../modalsComponents/components/checkout-modal/utils';
import { checkStripeSession, createCheckoutSession } from '../api';
import { STRIPE_API_KEY } from '../constants';
import { IStripeError } from '../interfaces';

const isElectron = !!window.require;

interface IStripeForm {
  plans: IPlan[];
  hasSuccessPayment: boolean;
  selectedPlan: string;
  selectedDiscount: string;
  needChangePlan: boolean;
  getCheckmarkLine: () => JSX.Element;
  setShowSuccessModal: Dispatch<SetStateAction<boolean>>;
  workspaceId?: string;
  setThreeDSecureUrl: Dispatch<SetStateAction<string>>;
  setShowThreeDSecureModal: Dispatch<SetStateAction<boolean>>;
  setIsCloseModalDisabled: Dispatch<SetStateAction<boolean>>;
  setIsLoadingModalShown: Dispatch<SetStateAction<boolean>>;
  setClosePaymentModal: Dispatch<SetStateAction<boolean>>;
}

const StripeForm: FC<IStripeForm> = (props) => {
  const {
    selectedPlan,
    selectedDiscount,
    needChangePlan,
    hasSuccessPayment,
    plans,
    getCheckmarkLine,
    setShowSuccessModal,
    workspaceId,
    setThreeDSecureUrl,
    setShowThreeDSecureModal,
    setIsCloseModalDisabled,
    setIsLoadingModalShown,
    setClosePaymentModal,
  } = props;

  const tabCurrency = useCurrentTabShownCurrency();

  const [paymentInstant, setPaymentInstant] = useState<StripeManager|null>(null);

  const { profiles: userProfilesCount, _id, ...userContextPlan } = useContext(userContext);
  const { profilesCount: workspaceProfilesCount, updateWorkspace, updateAvailableWorkspace } = useContext(workspaceContext);

  const { t: translation } = useTranslate();

  const isAnnual = isDiscountAnnual(selectedDiscount);
  const planPeriod = isAnnual ? 'annual' : 'monthly';
  const profilesCount = NEW_FEATURES.workspaces ? workspaceProfilesCount : userProfilesCount;

  const query = useQuery();

  useEffect(() => {
    if (!NEW_FEATURES.newStripeCheckout) {
      return;
    }

    const checkoutStatus = query.get('status');
    const sessionId = query.get('session_id');

    query.delete('status');
    query.delete('session_id');
    if (!checkoutStatus) {
      return;
    }

    if (checkoutStatus === 'success' && sessionId) {
      checkSession(sessionId);
    }
  }, []);

  const checkSession = (sessionId: string): void => {
    setIsLoadingModalShown(true);
    checkStripeSession(sessionId)
      .then(checkTransactionResponse => {
        setShowSuccessModal(true);
        const { paymentId = '', userId, amount, planId, planName: checkoutPlanName } = checkTransactionResponse;
        sendGA4PurchaseEvent({
          userId,
          amount,
          planId,
          planName: checkoutPlanName,
          paymentId,
        });

        setIsLoadingModalShown(false);
        setShowSuccessModal(true);
      })
      .catch(error => {
        setClosePaymentModal(true);
        const errorMessage = error?.message || error?.body?.message || '';
        sendErrorPaymentAnalytics(
          errorMessage,
          {},
          'stripe',
          {
            paymentMethod: 'Stripe',
            description: errorMessage || 'undefined error',
          },
        );

        if (!errorMessage) {
          return;
        }

        message.error(errorMessage);
      })
      .finally(() => {
        setIsLoadingModalShown(false);
      });
  };

  const setThreeDSecureModal = (status: boolean): void => {
    setIsCloseModalDisabled(status);
    setShowThreeDSecureModal(status);
  };

  const stripeForm = (stripeProps: any): JSX.Element => {
    const [isBtnLoading, setIsBtnLoading] = useState<boolean>(false);
    const [recurringPayments, setRecurringPayments] = useState<boolean>(true);

    const handleSubmit = async (event: MouseEvent<HTMLButtonElement>): Promise<void> => {
      sendActionAnalytics('clicked pay now', { actionInfo: 'stripe', paymentMethod: 'stripe' }).catch(() => null);
      event.preventDefault();
      if (hasSuccessPayment) {
        history.replace('/profileList');

        return;
      }

      const planToPay = getSelectedPlan(plans, selectedPlan);
      const profilesStatus = planToPay && checkPlanMaxProfiles({ translation, planToPay, profiles: profilesCount, paymentMethod: 'stripe' });
      if (!profilesStatus) {
        return;
      }

      if (NEW_FEATURES.newStripeCheckout) {
        handleCheckout(event);

        return;
      }

      const amount = planToPay.priceForMonth;
      if (amount && amount < 50) {
        message.error(translation('notifications.error.wrongStripeAmount'));
        sendErrorPaymentAnalytics(
          translation('notifications.error.wrongStripeAmount'),
          {
            amount,
            plan: planToPay.name,
            planPeriod,
            scenario: 'wrongStripeAmount',
          },
          'stripe',
        );
        history.replace('/profileList');

        return;
      }

      const stripeManager = new StripeManager(
        selectedPlan,
        selectedDiscount,
        needChangePlan,
        setThreeDSecureUrl,
        setThreeDSecureModal,
        () => null,
        recurringPayments,
        getGoogleAnalyticsSessionId(),
        tabCurrency,
      );

      if (workspaceId) {
        stripeManager.setWorkspaceId(workspaceId);
      }

      try {
        setIsBtnLoading(true);
        const cardElement = stripeProps.elements.getElement('card');
        const createSource = await stripeProps.stripe.createSource({ type: 'card' });

        //       3  
        // const threeDSecureStatus = createSource.source?.card.three_d_secure;
        // if (threeDSecureStatus === 'not_supported') {
        //   sendErrorPaymentAnalytics(
        //     translation('notifications.error.threeDSecureUnavailable'),
        //     {
        //       amount,
        //       plan: planToPay.name,
        //       planPeriod,
        //       scenario: 'threeDSecureUnavailable',
        //     },
        //     'stripe',
        //     {
        //       paymentMethod: 'Stripe',
        //       description: '3D Secure is unavailable',
        //       amount,
        //       plan: planToPay.name,
        //     },
        //   );
        //
        //   history.replace('/profileList');
        //   message.error(translation('notifications.erorr.threeDSecureUnavailable'));
        //
        //   return;
        // }

        const cardErrorMessage = createSource.error?.message || '';
        const cardErrorCode = createSource.error?.code || '';
        cardErrorCode === 'card_declined' && sendErrorPaymentAnalytics(
          cardErrorMessage,
          {
            amount,
            plan: planToPay.name,
            planPeriod,
            scenario: 'card_declined',
          },
          'stripe',
        );
        if (cardErrorCode === 'card_declined' && /[--]/.test(cardErrorMessage)) {
          throw new Error(cardErrorMessage);
        }

        await stripeManager.postPaymentIntent();
        const cardPaymentRes = await stripeProps.stripe.confirmCardPayment(
          stripeManager.getClientSecret(),
          {
            payment_method: { card: cardElement },
            return_url: 'https://gologin.com/loading-payment',
          },
          { handleActions: false },
        );

        const { status, redirectUrl } = stripeManager.checkPaymentProcessStatus(cardPaymentRes);
        if (status === 'requires_action') {
          const receiveMessageFromIFrame = async ({ data: messageData }: MessageEvent): Promise<void> => {
            if (!messageData) {
              return;
            }

            if (messageData.message !== '3DS-authentication-complete') {
              return Promise.resolve();
            }

            window.removeEventListener('message', receiveMessageFromIFrame, false);
            try {
              await stripeManager.checkTransaction()
                .then(async (checkTransactionResponse) => {
                  let updateWorkspaceParams;
                  if (workspaceId) {
                    updateWorkspaceParams = {
                      workspaceId,
                      updateWorkspaceHooks: {
                        updateWorkspace,
                        updateAvailableWorkspace,
                      },
                    };
                  }

                  await updateSelectedPlan({ plans, selectedPlan, userContextPlan, updateWorkspaceParams });
                  setShowSuccessModal(true);

                  const { paymentId = '', paymentAmount = 0.0 } = checkTransactionResponse;
                  sendGA4PurchaseEvent({
                    userId: _id?.toString(),
                    amount: paymentAmount,
                    planId: selectedPlan,
                    planName: planToPay.name,
                    paymentId,
                  });
                })
                .catch((error: IStripeError) => {
                  message.error(error.message);
                  sendErrorPaymentAnalytics(
                    error.message,
                    {
                      amount,
                      plan: planToPay.name,
                      planPeriod,
                      scenario: 'requires_action',
                    },
                    'stripe',
                  );
                });
            } catch (error) {
              const errorMessage = (error as Error).message || error || '';
              message.error(errorMessage);
            }

            stripeManager.toggleModalWindow(false, '');
          };

          setPaymentInstant(stripeManager);
          window.addEventListener('message', receiveMessageFromIFrame, false);
          stripeManager.toggleModalWindow(true, redirectUrl);
        }

        if (status === 'succeeded') {
          try {
            const checkTransactionResponse = await stripeManager.checkTransaction()
              .catch((error: IStripeError) => {
                message.error(error.message);
                sendErrorPaymentAnalytics(
                  error.message,
                  {
                    amount,
                    plan: planToPay.name,
                    planPeriod,
                    scenario: 'succeeded',
                  },
                  'stripe',
                );
              });

            let updateWorkspaceParams;
            if (workspaceId) {
              updateWorkspaceParams = {
                workspaceId,
                updateWorkspaceHooks: {
                  updateWorkspace,
                  updateAvailableWorkspace,
                },
              };
            }

            await updateSelectedPlan({ plans, selectedPlan, userContextPlan, updateWorkspaceParams });
            setShowSuccessModal(true);

            const { paymentId = '', paymentAmount = 0.0 } = checkTransactionResponse;
            sendGA4PurchaseEvent({
              userId: _id?.toString(),
              amount: paymentAmount,
              planId: selectedPlan,
              planName: planToPay.name,
              paymentId,
            });
          } catch (error) {
            const errorMessage = (error as Error).message || error || '';
            message.error(errorMessage);
          }

          stripeManager.toggleModalWindow(false, '');
        }
      } catch (error: any) {
        const errorMessage = error?.message || error?.body?.message || '';
        sendErrorPaymentAnalytics(
          errorMessage,
          {
            amount,
            plan: planToPay.name,
            planPeriod,
          },
          'stripe',
          {
            paymentMethod: 'Stripe',
            description: errorMessage || 'undefined error',
            amount,
            plan: planToPay.name,
          },
        );

        if (!errorMessage) {
          return;
        }

        message.error(errorMessage);
      }

      setIsBtnLoading(false);
    };

    const finishBtnLoading = (): void => {
      setTimeout(() => {
        setIsBtnLoading(false);
      }, 3000);
    };

    const handleCheckout = async (event: MouseEvent<HTMLButtonElement>): Promise<void> => {
      setIsBtnLoading(true);
      const { url: checkoutUrl } = await createCheckoutSession({
        plan: selectedPlan,
        discount: selectedDiscount,
        workspaceId,
        gaSession: getGoogleAnalyticsSessionId(),
        currency: tabCurrency,
      });

      if (isElectron) {
        window.require('electron').shell.openExternal(checkoutUrl);
        finishBtnLoading();

        return;
      }

      window.open(checkoutUrl, '_blank');
      finishBtnLoading();
    };

    const sendFocusAnalytics = (): void => {
      sendActionAnalytics('clicked to card form', { actionInfo: 'stripe', paymentMethod: 'stripe' }).catch(() => null);
    };

    return (
      <Col>
        {!NEW_FEATURES.newStripeCheckout ? (
          <ParamsCardLineContainer>
            <PaymentDetailsContainer>
              <PaymentDetailsTxt>
                {translation('pricing.forms.stripe.enterPaymentDetails')}
              </PaymentDetailsTxt>
              <div style={{ display: 'flex', columnGap: '3px' }}>
                <PaymentDetailsTxt>
                  {translation('pricing.forms.stripe.poweredBy')}
                </PaymentDetailsTxt>
                <div>
                  <IconStripe />
                </div>
              </div>
            </PaymentDetailsContainer>
            <Row>
              <StyledCardElement
                hidePostalCode={true}
                onFocus={sendFocusAnalytics}
              />
            </Row>
          </ParamsCardLineContainer>
        ) : null}
        <ModalDivider />
        {getCheckmarkLine()}
        <ModalUpgradeButtonContainer paddingBottom={true}>
          <ModalUpgradeCardPaymentButton
            type='button'
            onClick={handleSubmit}
          >
            {isBtnLoading ?
              <Spinner size={19} thickness={2} /> :
              translation('pricing.forms.stripe.submitBtnTxt')}
          </ModalUpgradeCardPaymentButton>
        </ModalUpgradeButtonContainer>
      </Col>
    );
  };

  const InjectedStripeForm = injectStripe(stripeForm);

  return (
    <>
      <StripeProvider
        apiKey={STRIPE_API_KEY}
      >
        <Elements>
          <InjectedStripeForm />
        </Elements>
      </StripeProvider>
    </>
  );
};

export default StripeForm;
