import { useTranslate } from '@tolgee/react';
import React from 'react';

import {
  Container,
  Item,
  WrapperIcon,
} from './info-block.styles';
import IconPaymentModalCheckmark from '../../../../../ui/icons/IconPaymentModalCheckmark';

interface IInfoBlock {
  date: string;
  price: string;
  nextPlanName: string;
  paymentMethod: string;
  nextPaymentNewPlan: string;
}

const InfoBlock: React.FC<IInfoBlock> = (props): JSX.Element => {
  const { t: translation } = useTranslate();
  const { price, date, paymentMethod, nextPaymentNewPlan, nextPlanName } = props;

  let subscriptionKey = `upgradePlanModal.${paymentMethod}.text`;
  if (!['stripe', 'crypto', 'sigma'].includes(paymentMethod)) {
    subscriptionKey = 'upgradePlanModal.stripe.text';
  }

  const textDate = paymentMethod === 'crypto' ? nextPaymentNewPlan : date;

  return (
    <Container>
      <Item>
        <WrapperIcon>
          <IconPaymentModalCheckmark color='var(--00A987)' />
        </WrapperIcon>
        {translation('upgradePlanModal.body.noChargeNow')}
      </Item>
      <Item>
        <WrapperIcon>
          <IconPaymentModalCheckmark color='var(--00A987)' />
        </WrapperIcon>
        {translation('upgradePlanModal.body.planActiveImmediatelyText', { planName: nextPlanName })}
      </Item>
      <Item>
        <WrapperIcon>
          <IconPaymentModalCheckmark color='var(--00A987)' />
        </WrapperIcon>
        {translation(subscriptionKey, '', { price, date: textDate })}
      </Item>
    </Container>
  );
};

export default InfoBlock;
