import { useTranslate } from '@tolgee/react';
import React, { useEffect, useState } from 'react';

import {
  Plan,
  Container,
  PlanName,
  Description,
  Separator,
  AdditionalInfo,
} from './plans-block.styles';
import IconArrowDown2 from '../../../../../ui/icons/IconArrowDown2';
import { CurrentPlanUpgrade, NextPlanUpgrade } from '../content-modal/content-modal';

const FIRE = ' ';
const LIGHTNING = ' ';

interface IPlanBlock {
  currentPlan: CurrentPlanUpgrade;
  nextPlan: NextPlanUpgrade;
}

const PlansBlock: React.FC<IPlanBlock> = (props): JSX.Element => {
  const { currentPlan, nextPlan } = props;

  const { t: translation } = useTranslate();

  const { calculatedNumberDaysNextPlan } = nextPlan;
  const { calculatedNumberDaysCurrentPlan } = currentPlan;
  const switchMonthlyToAnnual = currentPlan.name === nextPlan.name && currentPlan.monthCount < nextPlan.monthCount;

  const getChargedText = (): string => {
    const differenceDays = calculatedNumberDaysCurrentPlan - calculatedNumberDaysNextPlan;
    let text = '';
    let emoji = FIRE;

    if (differenceDays === 0 && !switchMonthlyToAnnual) {
      text = translation('upgradePlanModal.body.chargedText2', {
        days: translation('upgradePlanModal.body.day', { count: calculatedNumberDaysNextPlan }),
      });
    } else if (switchMonthlyToAnnual) {
      text = translation('upgradePlanModal.body.chargedText3', {
        days: translation('upgradePlanModal.body.day', { count: calculatedNumberDaysCurrentPlan }),
      });

      emoji = LIGHTNING;
    } else if (calculatedNumberDaysNextPlan === 0) {
      text = translation('upgradePlanModal.newPlan.recalculationOfDays', {
        days: translation('upgradePlanModal.body.day', { count: calculatedNumberDaysCurrentPlan }),
      });
    } else {
      text = translation('upgradePlanModal.body.chargedText1', {
        chargedCountDays: translation('upgradePlanModal.body.day', { count: differenceDays }),
        newEstimatedNumberDays: translation('upgradePlanModal.body.day', { count: calculatedNumberDaysNextPlan }),
        oldNumberDays: calculatedNumberDaysCurrentPlan,
      });
    }

    return `${emoji}${text}`;
  };

  const getPaymentPeriodText = (monthCount: number): string => {
    if (monthCount === 12) {
      return translation('upgradePlanModal.body.periodYearly');
    }

    return translation('upgradePlanModal.body.periodMonthly');
  };

  const currentPrice = translation('upgradePlanModal.body.billedPeriodText', {
    price: currentPlan.monthPrice,
    period: getPaymentPeriodText(currentPlan.monthCount),
  });

  const nextPrice = translation('upgradePlanModal.body.billedPeriodText', {
    price: nextPlan.monthPrice,
    period: getPaymentPeriodText(nextPlan.monthCount),
  });

  const nextPaymentCurrentPlan = translation('upgradePlanModal.body.nextPaymentText', {
    price: currentPlan.fullPrice,
    date: currentPlan.nextPaymentDate,
  });

  const nextPaymentNewPlan = translation('upgradePlanModal.body.nextPaymentText', {
    price: nextPlan.fullPrice,
    date: nextPlan.nextPaymentDate,
  });

  return (
    <>
      <Plan>
        <Container>
          <PlanName>
            {translation('upgradePlanModal.body.currentPlan')}
          </PlanName>
          <div>
            <Description bold={true} crossedOut={true}>
              {currentPlan.name}
            </Description>
            <Description crossedOut={true}>
              {currentPrice}
            </Description>
            <Description crossedOut={true}>
              {nextPaymentCurrentPlan}
            </Description>
          </div>
        </Container>
      </Plan>
      <Separator>
        <IconArrowDown2 />
      </Separator>

      <Plan background='var(--ECF7FD)'>
        <Container>
          <PlanName>
            {translation('upgradePlanModal.body.newPlan')}
          </PlanName>
          <div>
            <Description bold={true} color='var(--36363D)'>
              {nextPlan.name}
            </Description>
            <Description color='var(--36363D)'>
              {nextPrice}
            </Description>
            <Description color='var(--36363D)'>
              {nextPaymentNewPlan}
            </Description>
          </div>
        </Container>

        <AdditionalInfo>
          {getChargedText()}
        </AdditionalInfo>
      </Plan>
    </>
  );
};

export default PlansBlock;
