import * as Sentry from '@sentry/react';
import { Icon } from 'antd';
import moment from 'moment';
import React, { FC, useContext, useEffect, useMemo, useState } from 'react';

import { CustomPaymentModal, LoadingContainer } from './../domElements';
import ContentModal, { CurrentPlanUpgrade, NextPlanUpgrade } from './content-modal/content-modal';
import { ContainerContent } from './upgrade-plan-modal.styles';
import { IPlan } from '../../../../interfaces/plan';
import { userContext, workspaceContext } from '../../../../state';
import { ReactError } from '../../../../utils/sentry-parameters/custom-errors';
import { sendActionAnalytics } from '../../../common/api';
import { ANNUAL_DISCOUNT, RU_LOCALE } from '../../../modalsComponents/components/checkout-modal/constants';
import { getSelectedPlan } from '../../../modalsComponents/components/checkout-modal/utils';
import SuccessPaymentModal from '../../../modalsComponents/components/success-payment-modal';
import { upgradePlan } from '../../api';
import { IGetCorrectPlanPrice, IGetPlanPrice } from '../../interfaces';

const PRICING_DATE_FORMAT = 'MMM D YYYY';

interface IUpgradePlanModal {
  modalVisible: boolean;
  selectedPlanId: string;
  selectedDiscount: string;
  plans: IPlan[];
  closePaymentModal: () => void;
  getPlanPrice: (params: IGetPlanPrice) => string;
  getPlanInfo: (planId: string, field: keyof IPlan) => JSX.Element | string;
  workspaceId: string;
}

const UpgradePlanModal: FC<IUpgradePlanModal> = (props) => {
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [isLoadingModalShown, setIsLoadingModalShown] = useState(false);
  const [currentPlanName, setCurrentPlanName] = useState('');
  const [nextPaymentCurrentPlan, setNextPaymentCurrentPlan] = useState('');
  const [currentPlanId, setCurrentPlanId] = useState('');
  const [nextPaymentNewPlan, setNextPaymentNewPlan] = useState('');
  const [totalPrice, setTotalPrice] = useState('');
  const [calculatedNumberDaysCurrentPlan, setCalculatedNumberDaysCurrentPlan] = useState(0);
  const [calculatedNumberDaysNextPlan, setCalculatedNumberDaysNextPlan] = useState(0);

  const {
    modalVisible,
    selectedPlanId,
    selectedDiscount,
    plans,
    closePaymentModal,
    getPlanPrice,
    getPlanInfo,
    workspaceId,
  } = props;

  const { updateUser, plan, payment } = useContext(userContext);
  const {
    updateWorkspace,
    updateAvailableWorkspace,
    planName,
    planId,
    planExpiresAt,
    paymentMethod: workspacePaymentMethod,
    paymentDiscount,
  } = useContext(workspaceContext);

  let { discountId: currentDiscount, paymentMethod } = payment;

  if (workspaceId) {
    currentDiscount = paymentDiscount;
    paymentMethod = workspacePaymentMethod;
  }

  const isRuPrices = workspacePaymentMethod === 'sigma';
  const country = isRuPrices ? RU_LOCALE : '';

  const monthsViaDiscount = (discountId: string): number => discountId === ANNUAL_DISCOUNT ? 12 : 1;
  const discountFactor = (discountId: string): number => discountId === ANNUAL_DISCOUNT ? 0.5 : 1;

  useEffect(() => {
    if (modalVisible) {
      sendActionAnalytics('visited payment methods page - upgrade', {}).catch(() => null);
    }
  }, [modalVisible]);

  useEffect(() => {
    if (workspaceId && planName) {
      setCurrentPlanName(planName);

      return;
    }

    setCurrentPlanName(plan.name);
  }, [planName, workspaceId]);

  useEffect(() => {
    if (workspaceId && planId) {
      setCurrentPlanId(planId);

      return;
    }

    setCurrentPlanId(plan._id || plan.id);
  }, [planId, workspaceId]);

  useEffect(() => {
    const price = getCorrectPlanPrice({ planId: selectedPlanId, discountId: selectedDiscount, locale: country });
    setTotalPrice(price);
  }, [selectedPlanId, selectedDiscount]);

  useEffect(() => {
    let currentPaymentExpiresIn = payment.expiresIn;

    if (workspaceId) {
      currentPaymentExpiresIn = planExpiresAt;
    }

    setNextPaymentCurrentPlan(moment(currentPaymentExpiresIn).format(PRICING_DATE_FORMAT));
  }, [payment.expiresIn, workspaceId]);

  const getCalculatedPlanPrice = (id: string, discount: string, field: keyof IPlan): number => (
    Math.floor(+getPlanInfo(id, field) * discountFactor(discount) / 100)
  );

  const getCorrectPlanPrice = (priceParams: IGetCorrectPlanPrice): string => {
    const { planId, discountId, locale } = priceParams;
    const months = monthsViaDiscount(discountId);
    const valueDiscountFactor = discountFactor(discountId);
    const priceField = isRuPrices ? 'priceForMonthRUB' : 'priceForMonth';
    const planPrice = Math.floor(+getPlanInfo(planId, priceField) * valueDiscountFactor) * months / 100;

    return getPriceFormat(planPrice, locale);
  };

  useEffect(() => {
    let { expiresIn: currentPaymentExpiresIn } = payment;

    if (workspaceId) {
      currentPaymentExpiresIn = planExpiresAt;
    }

    const switchMontlyToAnnual = currentPlanId === selectedPlanId && selectedDiscount === ANNUAL_DISCOUNT;

    const currentPlanPrice = getCalculatedPlanPrice(currentPlanId, currentDiscount, 'priceForMonth');
    const timeLeftInDays = moment(currentPaymentExpiresIn).diff(moment.utc().toDate(), 'days');
    const upgradePlanPrice = getCalculatedPlanPrice(selectedPlanId, currentDiscount, 'priceForMonth');
    const prorateTimeDays = timeLeftInDays * currentPlanPrice / upgradePlanPrice;
    const nextDate = switchMontlyToAnnual ?
      moment(currentPaymentExpiresIn).format(PRICING_DATE_FORMAT) :
      moment().add(prorateTimeDays, 'days').format(PRICING_DATE_FORMAT);

    setCalculatedNumberDaysCurrentPlan(Math.round(timeLeftInDays));
    setCalculatedNumberDaysNextPlan(Math.round(prorateTimeDays));
    setNextPaymentNewPlan(nextDate);
  }, [payment.expiresIn, currentPlanId, selectedDiscount, selectedPlanId, currentDiscount, planExpiresAt]);

  const getPriceFormat = (planPrice: number, country?: string) => {
    if (country === RU_LOCALE) {
      return new Intl.NumberFormat('ru-RU', {
        style: 'currency',
        currency: 'RUB',
        minimumFractionDigits: 0,
      }).format(planPrice);
    }

    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
    }).format(planPrice);
  };

  const handleUpgradePlan = async (): Promise<void> => {
    setIsLoadingModalShown(true);
    const updatePlanData = await upgradePlan(selectedPlanId, selectedDiscount, workspaceId)
      .catch(err => {
        const errorMessage = err instanceof Error ? err.message : 'Plan upgrade error';
        Sentry.captureException(new ReactError(errorMessage), (scope) => {
          scope.setLevel('error');
          scope.setTransactionName('plan-upgrage');
          scope.setFingerprint(['plan-upgrade']);

          return scope;
        });

        setIsLoadingModalShown(false);
        closePaymentModal();
      });

    if (!updatePlanData) {
      return;
    }

    const { planId: updatePlanId, nextPaymentDate } = updatePlanData;

    updateUser({
      plan: getSelectedPlan(plans, updatePlanId),
      planExpireDate: nextPaymentDate,
    });

    updateWorkspace({
      planExpiresAt: moment(nextPaymentDate).utc().toDate(),
    });

    updateAvailableWorkspace(workspaceId, {
      planId: updatePlanId,
      visibleExpiresDate: moment(nextPaymentDate).utc().toDate(),
      planName: getPlanInfo(updatePlanId, 'name'),
      planProfilesMax: +getPlanInfo(updatePlanId, 'maxProfiles'),
    });

    setShowSuccessModal(true);
    setIsLoadingModalShown(false);
  };

  const currentPlanData: CurrentPlanUpgrade = useMemo(() => ({
    name: currentPlanName,
    monthPrice: getPlanPrice({
      planId: currentPlanId,
      monthCount: monthsViaDiscount(currentDiscount),
      locale: country,
    }),
    fullPrice: getCorrectPlanPrice({
      planId: currentPlanId,
      discountId: currentDiscount,
      locale: country,
    }),
    nextPaymentDate: nextPaymentCurrentPlan,
    monthCount: monthsViaDiscount(currentDiscount),
    calculatedNumberDaysCurrentPlan,
  }), [currentPlanName, currentPlanId, currentDiscount, nextPaymentCurrentPlan, calculatedNumberDaysCurrentPlan]);

  const nextPlanData: NextPlanUpgrade = useMemo(() => ({
    name: getPlanInfo(selectedPlanId, 'name'),
    monthPrice: getPlanPrice({
      planId: selectedPlanId,
      monthCount: monthsViaDiscount(selectedDiscount),
      locale: country,
    }),
    fullPrice: totalPrice,
    nextPaymentDate: nextPaymentNewPlan,
    monthCount: monthsViaDiscount(selectedDiscount),
    calculatedNumberDaysNextPlan,
  }), [selectedPlanId, totalPrice, nextPaymentNewPlan, selectedDiscount, calculatedNumberDaysNextPlan]);

  const renderModals = (): JSX.Element => {
    if (showSuccessModal) {
      return (
        <SuccessPaymentModal
          modalVisible={showSuccessModal}
          selectedPlan={selectedPlanId}
          getPlanInfo={getPlanInfo}
          styles={{ boxShadow: 'none', paddingBottom: 354 }}
        />
      );
    }

    return (
      <ContentModal
        onClickUpgrade={handleUpgradePlan}
        onCloseModal={closePaymentModal}
        currentPlan={currentPlanData}
        nextPlan={nextPlanData}
        paymentMethod={paymentMethod}
      >
        {isLoadingModalShown ? (
          <LoadingContainer>
            <Icon type='loading' />
          </LoadingContainer>
        ) : null}
      </ContentModal>
    );
  };

  return (
    <CustomPaymentModal
      width={520}
      title={null}
      footer={null}
      closable={false}
      maskClosable={true}
      onCancel={closePaymentModal}
      visible={modalVisible}
      bodyStyle={{ padding: 0 }}
    >
      <ContainerContent>
        {renderModals()}
      </ContainerContent>
    </CustomPaymentModal>
  );
};

export default UpgradePlanModal;
