import styled from '@emotion/styled';

export const Container = styled('div')`
    position: relative;
    color: var(--36363D);
    font-family: Roboto;
    font-size: 24px;
    line-height: 32px;
    padding: 40px;
`;

export const Footer = styled('div')`
    text-align: center;
    margin-top: 28px;
`;

export const ModalUpgradeButton = styled('button')`
  font-family: Roboto;
  border: 1px solid var(--00A987);
  border-radius: 60px;
  outline: none;
  cursor: pointer;
  font-size: 18px;
  line-height: 22px;
  background: var(--00A987);
  color: var(--FFFFFF);
  width: 166px;
  height: 56px;

  &:hover {
    background: var(--00997A);
  }
`;

export const ContainerCrossIcon = styled('div')`
  .MuiSvgIcon-root {
    cursor: pointer;
    position: absolute;
    top: 16px;
    right: 16px;
    color: var(--81818A);
    width: 30px;
    height: 30px;
    opacity: 0.5;
  }
`;

export const Wrapper = styled('div')`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: var(--FFFFFF)
`;
