import { useTranslate } from '@tolgee/react';
import React from 'react';

import { Container } from './modal-title.styles';

const ModalTitle: React.FC = (): JSX.Element => {
  const { t: translation } = useTranslate();

  return (
    <Container>
      {translation('upgradePlanModal.title.text')}
    </Container>
  );
};

export default ModalTitle;
