import { IPaymentBaseClass, PaymentBaseClass } from './payment-base-class';
import {
  changePlan,
  IPostPaymentIntent,
  postPaymentIntent,
  postPaymentIntentResult,
} from '../pricing/api';
import { E_CURRENCY, IStripeApi, IStripeError, TCurrency } from '../pricing/interfaces';

interface IStripeClass extends IPaymentBaseClass {
  paymentIntentId?: string;
  clientSecret?: string;
  setForRecurringPayments: boolean;
  getClientSecret: () => void;
  postPaymentIntent: () => void;
  checkPaymentProcessStatus: (response: any) => void;
  checkTransaction: (fromModalWindow?: boolean) => Promise<void>;
}

class StripeApi implements IStripeApi {
  public postPaymentIntent(opts: IPostPaymentIntent): Promise<any> {
    return postPaymentIntent(opts);
  }

  public postPaymentIntentResult(
    paymentIntentId: string,
    plan: string,
    discount: string,
    setForRecurringPayments: boolean,
    workspaceId?: string,
    currency?: TCurrency,
  ): Promise<any> {
    return postPaymentIntentResult({ paymentIntentId, plan, discount, setForRecurringPayments, workspaceId, currency });
  }
}

// tslint:disable-next-line:max-classes-per-file
export class StripeManager extends PaymentBaseClass implements IStripeClass {
  protected paymentIntentId: string|undefined;
  protected clientSecret: string|undefined;
  protected stripeApi: StripeApi|undefined;
  protected workspaceId: string|undefined;

  constructor(
    private plan: string,
    private discount: string,
    private needChangePlan: boolean,
    private setModalUrl: (url: string) => void,
    private toggleModal: (status: boolean) => void,
    private redirectTo: () => void,
    private readonly setForRecurringPayments: boolean,
    private gaSession: string,
    private currency: TCurrency,
  ) {
    super(plan, discount, needChangePlan, setModalUrl, toggleModal, redirectTo);
    this.stripeApi = new StripeApi();
    this.setForRecurringPayments = setForRecurringPayments;
    this.currency = currency || E_CURRENCY.USD;
  }

  public getClientSecret() {
    return this.clientSecret;
  }

  public async postPaymentIntent() {
    if (!this.stripeApi) {
      return;
    }

    const paymentIntentRes = await this.stripeApi.postPaymentIntent({
      plan: this.plan,
      discount: this.discount,
      setForRecurringPayments: this.setForRecurringPayments,
      workspaceId: this.workspaceId,
      gaSession: this.gaSession,
      currency: this.currency,
    });

    const { clientSecret } = paymentIntentRes;
    const { id } = paymentIntentRes;
    if (!(clientSecret && id)) {
      throw new Error('Something went wrong. Try again later');
    }

    this.clientSecret = clientSecret;
    this.paymentIntentId = id;
  }

  public async checkTransaction() {
    const errorObj: IStripeError = {
      status: 'failed',
      message: 'Payment failed. Try again or change card',
      manualError: true,
    };

    if (!(this.paymentIntentId && this.stripeApi)) {
      return Promise.reject(errorObj);
    }

    if (this.needChangePlan) {
      try {
        await this.changePlan(this.plan);
      } catch (e) {}
    }

    const paymentResult = await this.stripeApi.postPaymentIntentResult(
      this.paymentIntentId,
      this.plan,
      this.discount,
      this.setForRecurringPayments,
      this.workspaceId,
      this.currency,
    );

    errorObj.message = paymentResult.errorMessage;

    const status = paymentResult.paymentStatus;
    if (!['processing', 'succeeded'].includes(status)) {
      return Promise.reject(errorObj);
    }

    this.makeRedirect();

    return Promise.resolve(paymentResult);
  }

  public checkPaymentProcessStatus(cardPaymentRes: any) {
    const { error } = cardPaymentRes;
    if (error) {
      throw new Error(error.message || error);
    }

    const paymentIntentObj = cardPaymentRes.paymentIntent;
    if (!paymentIntentObj) {
      throw new Error('No payment process created');
    }

    const { status } = paymentIntentObj;
    if (status === 'requires_payment_method') {
      throw new Error('No 3D secure supported. Try again or change card');
    }

    return {
      status,
      redirectUrl: paymentIntentObj.next_action?.redirect_to_url?.url,
    };
  }

  setWorkspaceId(workspaceId: string): void {
    this.workspaceId = workspaceId;
  }

  public changePlan(planId: string) {
    return changePlan(planId, this.workspaceId);
  }
}
