import { CloseOutlined } from '@material-ui/icons';
import { useTranslate } from '@tolgee/react';
import React from 'react';

import { Container, ContainerCrossIcon, Footer, ModalUpgradeButton, Wrapper } from './content-modal.styles';
import InfoBlock from '../info-block/info-block';
import ModalTitle from '../modal-title/modal-title';
import PlansBlock from '../plans-block/plans-block';

export interface IPlanUpgrade {
  name: string;
  fullPrice: string;
  monthPrice: string;
  nextPaymentDate: string;
  monthCount: number;
  calculatedNumberDaysCurrentPlan: number;
  calculatedNumberDaysNextPlan: number;
  children?: React.ReactElement;
}

export type CurrentPlanUpgrade = Omit<IPlanUpgrade, 'calculatedNumberDaysNextPlan'>;
export type NextPlanUpgrade = Omit<IPlanUpgrade, 'calculatedNumberDaysCurrentPlan'>;

interface IContentModal {
  currentPlan: CurrentPlanUpgrade;
  nextPlan: NextPlanUpgrade;
  paymentMethod: string;
  onClickUpgrade: () => Promise<void>;
  onCloseModal: () => void;
}

const ContentModal: React.FC<IContentModal> = (props): JSX.Element => {
  const { t: translation } = useTranslate();
  const {
    currentPlan,
    nextPlan,
    paymentMethod,
    children,
    onClickUpgrade,
    onCloseModal,
  } = props;

  return (
    <Container>
      <ContainerCrossIcon>
        <CloseOutlined onClick={onCloseModal} />
      </ContainerCrossIcon>
      <ModalTitle />
      <PlansBlock nextPlan={nextPlan} currentPlan={currentPlan} />
      <InfoBlock
        price={nextPlan.fullPrice}
        date={nextPlan.nextPaymentDate}
        paymentMethod={paymentMethod}
        nextPaymentNewPlan={nextPlan.nextPaymentDate}
        nextPlanName={nextPlan.name}
      />
      {children ? (
        <Wrapper>
          {children}
        </Wrapper>
      ) : null}
      <Footer>
        <ModalUpgradeButton onClick={onClickUpgrade}>
          {translation('upgradePlanModal.bottom.upgradeNowBtnText')}
        </ModalUpgradeButton>
      </Footer>
    </Container>
  );
};

export default ContentModal;
