/* eslint-disable max-len */
import * as React from 'react';

import { IIconWrapper, IconWrapper } from './wrapper';

const IconTelegram = (props: IIconWrapper): JSX.Element => (
  <IconWrapper {...props}>
    <svg
      width={36}
      height={36}
      viewBox="0 0 36 36"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      <rect width={36} height={36} rx={18} fill="url(#tga)" />
      <defs>
        <pattern
          id="tga"
          patternContentUnits="objectBoundingBox"
          width={1}
          height={1}
        >
          <use xlinkHref="#tgb" transform="scale(.00444)" />
        </pattern>
        <image
          id="tgb"
          width={225}
          height={225}
          xlinkHref="data:image/png;base64,"
        />
      </defs>
    </svg>
  </IconWrapper>
);

export default IconTelegram;
